import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { useAuth } from '../context/AuthContext';
import axios from 'axios';

const HomePage = () => {
  const { user } = useAuth();
  const [stats, setStats] = useState({
    users: 0,
    tasks: 0,
    projects: 0,
    messages: 0
  });

  useEffect(() => {
    //   
    const fetchStats = async () => {
      try {
        const response = await axios.get('/api/stats');
        setStats(response.data);
      } catch (error) {
        console.error('   :', error);
      }
    };

    fetchStats();
  }, []);

  //       
  const getDashboardPath = () => {
    if (!user) return '/signin';
    
    switch (user.role) {
      case 'admin':
        return '/dashboard';
      case 'student':
        return '/student-dashboard';
      default:
        return '/dashboard';
    }
  };

  return (
    <div className="min-h-screen bg-gradient-to-b from-blue-50 to-white">
      {/*   */}
      <header className="bg-white shadow-sm">
        <div className="container mx-auto px-4 py-4 flex justify-between items-center">
          <h1 className="text-2xl font-bold text-blue-600">   </h1>
          <div>
            {user ? (
              <Link 
                to={getDashboardPath()}
                className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-md transition-colors"
              >
                 
              </Link>
            ) : (
              <div className="space-x-2 rtl:space-x-reverse">
                <Link 
                  to="/signin"
                  className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-md transition-colors"
                >
                   
                </Link>
                <Link 
                  to="/signup"
                  className="border border-blue-600 text-blue-600 hover:bg-blue-50 px-4 py-2 rounded-md transition-colors"
                >
                   
                </Link>
              </div>
            )}
          </div>
        </div>
      </header>

      {/*   */}
      <main>
        {/*   */}
        <section className="py-16 text-center">
          <div className="container mx-auto px-4">
            <h2 className="text-4xl font-bold mb-6">      </h2>
            <p className="text-xl text-gray-600 mb-8 max-w-3xl mx-auto">
                        .
            </p>
            <Link 
              to={getDashboardPath()}
              className="bg-blue-600 hover:bg-blue-700 text-white text-lg px-6 py-3 rounded-md transition-colors"
            >
              {user ? '   ' : ' '}
            </Link>
          </div>
        </section>

        {/*   */}
        <section className="py-12 bg-white">
          <div className="container mx-auto px-4">
            <h3 className="text-2xl font-semibold text-center mb-10"> </h3>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
              <div className="bg-blue-50 p-6 rounded-lg text-center">
                <div className="text-3xl font-bold text-blue-600 mb-2">{stats.users}</div>
                <div className="text-gray-600"></div>
              </div>
              <div className="bg-green-50 p-6 rounded-lg text-center">
                <div className="text-3xl font-bold text-green-600 mb-2">{stats.tasks}</div>
                <div className="text-gray-600"></div>
              </div>
              <div className="bg-purple-50 p-6 rounded-lg text-center">
                <div className="text-3xl font-bold text-purple-600 mb-2">{stats.projects}</div>
                <div className="text-gray-600"></div>
              </div>
              <div className="bg-amber-50 p-6 rounded-lg text-center">
                <div className="text-3xl font-bold text-amber-600 mb-2">{stats.messages}</div>
                <div className="text-gray-600"></div>
              </div>
            </div>
          </div>
        </section>

        {/*   */}
        <section className="py-16">
          <div className="container mx-auto px-4">
            <h3 className="text-2xl font-semibold text-center mb-10"> </h3>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
              <div className="bg-white p-6 rounded-lg shadow-md">
                <h4 className="text-xl font-semibold mb-3"> </h4>
                <p className="text-gray-600">
                              .
                </p>
              </div>
              <div className="bg-white p-6 rounded-lg shadow-md">
                <h4 className="text-xl font-semibold mb-3"> </h4>
                <p className="text-gray-600">
                            .
                </p>
              </div>
              <div className="bg-white p-6 rounded-lg shadow-md">
                <h4 className="text-xl font-semibold mb-3"> </h4>
                <p className="text-gray-600">
                            .
                </p>
              </div>
            </div>
          </div>
        </section>
      </main>

      {/*  */}
      <footer className="bg-gray-800 text-white py-8">
        <div className="container mx-auto px-4 text-center">
          <p>&copy; {new Date().getFullYear()}    .   .</p>
        </div>
      </footer>
    </div>
  );
};

export default HomePage;
